import React, { ComponentType } from 'react';
import Loadable from 'react-loadable';
import { CircularProgress, Grid } from '@material-ui/core';

const Loading = () => (
  <Grid container justify="center">
    <Grid item>
      <CircularProgress color="primary" />
    </Grid>
  </Grid>
);

const options = {
  loading: Loading,
  delay: 400,
};

const lazy = <P extends {}>(
  loader: () => Promise<ComponentType<P> | { default: ComponentType<P> }>,
) =>
  Loadable({
    loader,
    ...options,
  });

export const CinderellaBook = lazy(() => import('./CinderellaBook'));

export const KickstarterLanding = lazy(() => import('./KickstarterLanding'));

export const LoginRedesign = lazy(() => import('./LoginRedesign'));

export const MobileAppOnboarding = lazy(() => import('./MobileAppOnboarding'));

export const ParkingMeter = lazy(() => import('./ParkingMeter'));

export const WomanIllustration = lazy(() => import('./WomanIllustration'));
