import React from 'react';
import classNames from 'classnames';
import { makeStyles, createStyles } from '@material-ui/core';
import { useInView } from 'react-hook-inview';

type Props = {
  figure: React.ComponentType<any>;
  animated?: 'stroke-draw';
  classes?: Partial<Classes>;
};

type Classes = ReturnType<typeof useStyles>;

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      element: {
        position: 'absolute',
        zIndex: -1,
      },

      stroke: {
        strokeDasharray: 1000,
        strokeDashoffset: 1000,
        animation: '$draw 3s linear forwards',
      },

      '@keyframes draw': {
        to: {
          strokeDashoffset: 0,
        },
      },
    }),
  {
    name: 'SectionFigure',
  },
);

export const SectionFigure: React.FC<Props> = (props) => {
  const classes = useStyles(props);
  const [ref, inView] = useInView();

  const { figure: Figure, animated } = props;

  return (
    <div className={classes.element} ref={animated && ref}>
      <Figure
        className={
          inView
            ? classNames({
                [classes.stroke]: animated === 'stroke-draw',
              })
            : undefined
        }
      />
    </div>
  );
};
