import React from 'react';
import ReactGA from 'react-ga';
import { Link, Typography, makeStyles, createStyles } from '@material-ui/core';
import { Section, SectionFigure } from 'src/components';
import useContent from 'src/content/useContent';
import { Rectangle, Ellipse, EllipseLine } from './shapes';

type Props = {
  id: string;
};

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      section: {
        paddingTop: theme.spacing(7),
        paddingBottom: theme.spacing(8),

        [theme.breakpoints.up('md')]: {
          paddingTop: theme.spacing(3),
          paddingBottom: theme.spacing(18),
        },
      },

      contentItem: {
        [theme.breakpoints.down('sm')]: {
          display: 'inline',
        },
      },

      rectangle: {
        left: theme.spacing(6),

        [theme.breakpoints.down('sm')]: {
          bottom: theme.spacing(1),
          transform: 'scaleY(0.3)',
        },

        [theme.breakpoints.up('md')]: {
          top: 0,
        },
      },

      ellipse: {
        left: theme.spacing(-6),

        [theme.breakpoints.down('sm')]: {
          top: theme.spacing(-7),
        },

        [theme.breakpoints.up('md')]: {
          bottom: theme.spacing(0),
        },
      },

      ellipseLine: {
        left: theme.spacing(-1),

        [theme.breakpoints.down('sm')]: {
          top: theme.spacing(-5),
        },

        [theme.breakpoints.up('md')]: {
          bottom: theme.spacing(5),
        },
      },
    }),
  { name: 'ContactSection' },
);

const trackLink = (label: string) => () => {
  ReactGA.outboundLink({ label }, () => {});
};

export const ContactSection: React.FC<Props> = ({ id }) => {
  const classes = useStyles();
  const content = useContent();

  return (
    <Section
      id={id}
      title={content.contact.title}
      classes={{ section: classes.section }}
    >
      {content.contact
        .content({
          mail: (
            <Typography
              color="primary"
              variant="caption"
              component={Link}
              href={`mailto:${content.contact.mail}`}
              target="_blank"
              onClick={trackLink('Click Email')}
            >
              {content.contact.mail}
            </Typography>
          ),
          linkedin: (
            <Typography
              color="primary"
              variant="caption"
              component={Link}
              href={content.contact.linkedinUrl}
              target="_blank"
              onClick={trackLink('Click Linkedin')}
            >
              {content.contact.linkedin}
            </Typography>
          ),
        })
        .map((line, i) => (
          <Typography key={i} component="p" className={classes.contentItem}>
            {line}
          </Typography>
        ))}

      <SectionFigure
        figure={Rectangle}
        classes={{ element: classes.rectangle }}
      />
      <SectionFigure figure={Ellipse} classes={{ element: classes.ellipse }} />
      <SectionFigure
        animated="stroke-draw"
        figure={EllipseLine}
        classes={{ element: classes.ellipseLine }}
      />
    </Section>
  );
};
