import React from 'react';
import { Typography, makeStyles, createStyles } from '@material-ui/core';
import { Section, SectionFigure } from 'src/components';
import useContent from 'src/content/useContent';
import { Circle, DiagonalRectangle, Line, Rectangle } from './shapes';

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      section: {
        paddingTop: theme.spacing(4),
        paddingBottom: theme.spacing(18),

        [theme.breakpoints.up('md')]: {
          marginTop: theme.spacing(10),
        },
      },

      rectangle: {
        top: 0,
        left: theme.spacing(-5),

        [theme.breakpoints.up('md')]: {
          left: theme.spacing(10),
        },
      },

      circle: {
        top: theme.spacing(15),

        [theme.breakpoints.down('sm')]: {
          transform: 'scale(0.75)',
          right: theme.spacing(5),
        },

        [theme.breakpoints.up('md')]: {
          top: theme.spacing(1),
          left: theme.spacing(54),
        },
      },

      diagonalRectangle: {
        top: theme.spacing(11.5),
        right: theme.spacing(-17),

        [theme.breakpoints.down('sm')]: {
          transform: 'scale(0.75)',
        },

        [theme.breakpoints.up('md')]: {
          top: theme.spacing(2),
          right: 0,
        },
      },

      line: {
        top: theme.spacing(22),
        right: theme.spacing(-11),

        [theme.breakpoints.down('sm')]: {
          transform: 'scale(0.75)',
        },

        [theme.breakpoints.up('md')]: {
          top: theme.spacing(14),
          right: theme.spacing(6),
        },
      },
    }),
  { name: 'HeroSection' },
);

export const HeroSection: React.FC = () => {
  const classes = useStyles();
  const content = useContent();

  return (
    <Section
      title={content.hero.title.map((line) => (
        <span style={{ display: 'block' }} key={line}>
          {line}
        </span>
      ))}
      titleComponent="h1"
      classes={{ section: classes.section }}
    >
      <Typography component="p">{content.hero.subtitle}</Typography>

      <SectionFigure
        figure={DiagonalRectangle}
        classes={{ element: classes.diagonalRectangle }}
      />
      <SectionFigure
        figure={Rectangle}
        classes={{ element: classes.rectangle }}
      />
      <SectionFigure figure={Circle} classes={{ element: classes.circle }} />
      <SectionFigure
        animated="stroke-draw"
        figure={Line}
        classes={{ element: classes.line }}
      />
    </Section>
  );
};
