import React, { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { ProjectSlug } from 'types';
import {
  CinderellaBook,
  KickstarterLanding,
  LoginRedesign,
  MobileAppOnboarding,
  ParkingMeter,
  WomanIllustration,
} from './projects';
import { Typography, Grid } from '@material-ui/core';
import useContent from 'src/content/useContent';

const renderProject = (projectSlug: ProjectSlug | undefined) => {
  switch (projectSlug) {
    case 'cinderella-book':
      return <CinderellaBook />;

    case 'kickstarter-landing':
      return <KickstarterLanding />;

    case 'login-redesign':
      return <LoginRedesign />;

    case 'mobile-app-onboarding':
      return <MobileAppOnboarding />;

    case 'parking-meter':
      return <ParkingMeter />;

    case 'woman-illustration':
      return <WomanIllustration />;

    default:
      return undefined;
  }
};

export const ProjectPage: React.FC = () => {
  const { slug } = useParams();
  const content = useContent();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [slug]);

  const project = renderProject(slug as ProjectSlug | undefined);

  if (!project) {
    return (
      <Grid container justify="center">
        <Grid item>
          <Typography variant="h1">
            <Grid container justify="center">
              <Grid item component="span">
                404
              </Grid>
            </Grid>
          </Typography>
          <Typography variant="h1">{content.notFound.title}</Typography>
        </Grid>
      </Grid>
    );
  }

  return <div>{project}</div>;
};
