import React from 'react';
import { Link } from 'react-router-dom';
import classNames from 'classnames';
import { useInView } from 'react-hook-inview';
import { Typography, makeStyles, Grid, createStyles } from '@material-ui/core';
import { ProjectSlug } from 'types';
import { Arrow, Line } from './figures';

const duration = 0.5;

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      link: {
        color: 'unset',
        textDecoration: 'none',
      },

      container: {
        paddingTop: theme.spacing(3),
        paddingBottom: theme.spacing(2),
        transition: `background-color 0.5s ease, color 0.5s ease`,

        '&:hover': {
          backgroundColor: '#F4F9FF',
        },
      },

      imageContainer: {
        width: '100%',
        paddingTop: '67%',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
      },

      contentContainer: {
        height: '100%',
      },

      contentItem: {
        [theme.breakpoints.up('md')]: {
          display: 'flex',
          flexDirection: 'column',
          flex: 1,
        },
      },

      content: {
        [theme.breakpoints.up('md')]: {
          width: '58%',
          marginLeft: theme.spacing(8),
        },

        [theme.breakpoints.down('sm')]: {
          width: '85%',
          marginTop: theme.spacing(6),
          marginLeft: 'auto',
          marginRight: 'auto',
        },
      },

      title: {
        transform: 'translateY(-50%)',
      },

      description: {
        marginTop: theme.spacing(2),
      },

      relative: {
        position: 'relative',
      },

      figure: {
        opacity: 0,

        '&$visible': {
          opacity: 1,
        },
      },

      line: {
        position: 'absolute',
        transition: `transform ${duration}s ease, opacity ${duration}s ease`,

        [theme.breakpoints.up('md')]: {
          top: '49.7%',
          right: 0,
        },

        [theme.breakpoints.down('sm')]: {
          bottom: 0,
          left: '50%',
          transform: 'rotate(90deg) translateX(-100%)',
          transformOrigin: '0 0',
        },

        '& line': {
          stroke: theme.palette.common.black,
          transition: `stroke 0.5s ease`,
        },

        '&$visible': {
          transform: 'rotate(90deg) translateX(-50%)',

          [theme.breakpoints.up('md')]: {
            transform: 'translateX(50%)',
          },
        },
      },

      arrow: {
        transform: 'translateX(-50%)',
        transition: `transform ${duration}s ease ${duration}s, opacity ${duration}s ease ${duration}s`,

        '& path': {
          fill: theme.palette.common.black,
          transition: `fill 0.5s ease`,
        },

        '&$visible': {
          transform: 'none',
        },

        [theme.breakpoints.up('md')]: {
          position: 'absolute',
          bottom: '3%',
          right: '5%',
        },

        [theme.breakpoints.down('sm')]: {
          marginTop: theme.spacing(4),
          marginLeft: 'auto',
          marginRight: 'auto',
        },
      },

      visible: {},
    }),
  { name: 'ProjectCard' },
);

type Props = {
  title: string;
  description: string;
  slug: ProjectSlug;
  image: {
    src: string;
  };
};

export const ProjectCard: React.FC<Props> = ({
  title,
  description,
  slug,
  image,
}) => {
  const classes = useStyles();
  const [lineRef, lineInView] = useInView();
  const [arrowRef, arrowInView] = useInView();

  const inView = lineInView && arrowInView;

  return (
    <Link to={`/${slug}`} className={classNames(classes.link)}>
      <div className={classes.container}>
        <Grid container className={classes.relative}>
          <Grid item md={6} xs={12}>
            <div
              className={classNames(classes.relative, classes.imageContainer)}
              style={{ backgroundImage: `url(${image.src})` }}
            >
              <Line
                ref={lineRef}
                className={classNames(classes.figure, classes.line, {
                  [classes.visible]: inView,
                })}
              />
            </div>
          </Grid>
          <Grid item md={6} xs={12}>
            <Grid
              container
              direction="column"
              className={classes.contentContainer}
            >
              <Grid item className={classes.contentItem}></Grid>
              <Grid
                item
                className={classNames(classes.contentItem, classes.content)}
              >
                <Typography
                  variant="h3"
                  component="h3"
                  className={classes.title}
                >
                  {title}
                </Typography>
                <Typography component="p">{description}</Typography>
              </Grid>
            </Grid>
          </Grid>

          <Arrow
            ref={arrowRef}
            className={classNames(classes.figure, classes.arrow, {
              [classes.visible]: inView,
            })}
          />
        </Grid>
      </div>
    </Link>
  );
};
