import React, { useMemo } from 'react';
import { Switch, Route } from 'react-router-dom';
import { Container } from '@material-ui/core';
import {
  ExperienceSection,
  HeroSection,
  Nav,
  ContactSection,
  RecentWorkSection,
  ProjectPage,
  Copyright,
} from 'src/components';
import useContent from 'src/content/useContent';

const navIds = ['experience', 'recentWork', 'contact'] as const;

type NavId = typeof navIds[number];

const navId = navIds.reduce(
  (acc, e) => ({
    ...acc,
    [e]: e,
  }),
  {} as { [P in NavId]: P },
);

const App: React.FC = () => {
  const content = useContent();

  const navLinks = useMemo(
    () =>
      navIds.map((e) => ({
        id: e,
        text: content.nav[e],
      })),
    [content.nav],
  );

  return (
    <Container maxWidth="lg" disableGutters>
      <Nav links={navLinks} />

      <Switch>
        <Route path="/" exact>
          <HeroSection />
          <ExperienceSection id={navId.experience} />
          <RecentWorkSection id={navId.recentWork} />
          <ContactSection id={navId.contact} />
        </Route>

        <Route path="/:slug" exact>
          <ProjectPage />
        </Route>
      </Switch>

      <Copyright />
    </Container>
  );
};

export default App;
