import React from 'react';
import { Grid, makeStyles, createStyles } from '@material-ui/core';
import { NavLink } from 'src/components';

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      container: {
        backgroundColor: theme.palette.common.white,
        boxShadow: '0px 3px 4px rgba(210, 209, 255, 0.5)',
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        zIndex: 999999,
      },

      filler: {
        height: theme.spacing(2) * 2 + 20,
      },
    }),
  { name: 'Nav' },
);

type LinkItem = {
  id: string;
  text: string;
};

type Props = {
  links: LinkItem[];
};

export const Nav: React.FC<Props> = ({ links }) => {
  const classes = useStyles();

  return (
    <>
      <div className={classes.filler} />
      <Grid
        component="nav"
        container
        justify="space-evenly"
        className={classes.container}
      >
        {links.map((link) => (
          <Grid item key={link.id}>
            <Grid container>
              <NavLink anchorId={link.id}>{link.text}</NavLink>
            </Grid>
          </Grid>
        ))}
      </Grid>
    </>
  );
};
