import React from 'react';
import {
  Typography,
  Link,
  makeStyles,
  createStyles,
  Grid,
} from '@material-ui/core';
import { Section } from 'src/components';
import useContent from 'src/content/useContent';
import { CV } from './files';

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      experienceParagraph: {
        marginTop: theme.spacing(2),
      },

      experienceFooter: {
        marginTop: theme.spacing(1),
        flexDirection: 'column',
      },

      experienceFooterItem: {
        marginTop: theme.spacing(1),
      },

      section: {
        paddingTop: theme.spacing(5),
        marginBottom: theme.spacing(7),
      },
    }),
  {
    name: 'ExperienceSection',
  },
);

type Props = {
  id: string;
};

export const ExperienceSection: React.FC<Props> = ({ id }) => {
  const classes = useStyles();
  const content = useContent();

  return (
    <Section
      id={id}
      title={content.experience.title}
      classes={{ section: classes.section }}
    >
      {content.experience.paragraphs.map((paragraph, index) => (
        <Typography
          component="p"
          className={classes.experienceParagraph}
          key={index}
        >
          {paragraph}
        </Typography>
      ))}

      <Grid container className={classes.experienceFooter}>
        <Grid item className={classes.experienceFooterItem}>
          <Typography component="p" variant="caption">
            {content.experience.caption}
          </Typography>
        </Grid>
        <Grid item className={classes.experienceFooterItem}>
          <Typography color="primary" variant="caption">
            <Link href={CV} target="_blank">
              {content.experience.resume}
            </Link>
          </Typography>
        </Grid>
      </Grid>
    </Section>
  );
};
